.container{
    position: relative;
    width: 96%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
    
  .text{
    display: flex;
    flex-direction: column;
    width: 100%;
    color: rgb(163 152 135);
    font-weight: normal;
    padding: 1rem;
  }
    
  .read-or-hide{
    color: rgb(192,192,192);
    cursor: pointer;
  }
.App {
  text-align: center;
}

::-webkit-scrollbar {
  display: none;
}

#gradient__text {
  background: linear-gradient(89.97deg, #fdf7ee 1.84%, #d8875c 102.67%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
#bronze__text {
  background: linear-gradient(89.97deg, #f49867 1.84%, #0d69bd 102.67%);
