.mainContainer{
   background: orange;
}


.heading {
    background: -moz-radial-gradient(
        circle at 3% 25%,
        rgb(255 255 255) 0%,
        rgb(255 246 226) 100%
      );
    
      /* safari 5.1+,chrome 10+ */
      background: -webkit-radial-gradient(
        circle at 3% 25%,
        rgb(255 255 255) 0%,
        rgb(255 246 226) 100%
      );
    
      /* opera 11.10+ */
      background: -o-radial-gradient(
        circle at 3% 25%,
        rgb(255 255 255) 0%,
        rgb(255 246 226) 100%
      );
    
      /* ie 10+ */
      background: -ms-radial-gradient(
        circle at 3% 25%,
        rgb(255 255 255) 0%,
        rgb(255 246 226) 100%
      );
    
      /* global 92%+ browsers support */
      background: radial-gradient(
        circle at 3% 25%,
        rgb(255 255 255) 0%,
        rgb(255 246 226) 100%
      );
    }
    
